import Vue from 'vue'
import Router from 'vue-router'


import home from '@/pages/home'
import apiclouds from '@/pages/apiclouds'
import mine from '@/pages/mine'
import others from '@/pages/others'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: {
        name: 'home',
      }
    },
    {
      path: '/home',
      name: 'home',
      component: home
    },
    {
      path: '/apiclouds',
      name: 'apiclouds',
      component: apiclouds
    },
    {
      path: '/mine',
      name: 'mine',
      component: mine
    },
    {
      path: '/others',
      name: 'others',
      component: others
    }
    
  ]
})



