<template>
    <div class="home">
        <div class="banner">
            <mt-swipe :auto="4000">
                <mt-swipe-item>
                    <div class="banbox">
                        <img src="@/assets/image/1.jpg" alt="">
                    </div>
                </mt-swipe-item>
                <mt-swipe-item>
                    <div class="banbox">
                        <img src="@/assets/image/2.jpg" alt="">
                    </div>
                </mt-swipe-item>
                <mt-swipe-item>
                    <div class="banbox">
                        <img src="@/assets/image/3.jpg" alt="">
                    </div>
                </mt-swipe-item>
                <mt-swipe-item>
                    <div class="banbox">
                        <img src="@/assets/image/4.jpg" alt="">
                    </div>
                </mt-swipe-item>
                <mt-swipe-item>
                    <div class="banbox">
                        <img src="@/assets/image/5.jpg" alt="">
                    </div>
                </mt-swipe-item>
            </mt-swipe>
        </div>
        <div class="hlist">
            <div class="hlist_box"  v-for="item in listData">
                <router-link :to="{path:'/home'}">
                    <img :src="'http://www.linjianping.cn'+item.thumb" alt="">
                    <div>
                        <h4>{{item.title}}</h4>
                        <p><span>{{item.classify_tagname}}</span><span>{{item.publish_time}}</span></p>
                    </div>
                </router-link>
            </div>
        </div>
        <!-- <button @click="ajax_req">获取数据</button> -->
        <!-- <h3>{{t_height}}</h3> -->
        <!-- <h4>类型：{{data_type}}</h4> -->
    </div>
</template>
<script>
// import { Swipe, SwipeItem } from 'mint-ui';
import { Indicator,Toast } from 'mint-ui';
export default {
    name:'home',
    data(){
        return{
            loadingStatus: false,
            t_height:'',
            listData:'',
            data_type:'',
            curPages: 1,
        }
    },
    
    mounted(){
        var _this = this;
        this.ajax_req(1);
        this.t_height = api.safeArea.top;
        api.addEventListener({
            name:'scrolltobottom',
            extra:{
                threshold: 0           
            }
        }, function(ret, err){        
            // alert('已滚动到底部');
            var reqPage = ++ _this.curPages;
            _this.ajax_req(reqPage);
        });
    },
    methods: {
        ajax_req:function(pages){
            var _this = this;
            this.loadings();
            api.ajax({
                url: _this.host+'/api/index/get_article?page_num='+pages+'&select_value=All',
                // data:reqdata?reqdata:''
            }, function(ret, err) {
                console.log('有请求');
                if (ret) {
                    if(pages>1){
                        var allData = _this.listData;
                        if(ret.article.length>0){
                            for(var i=0;i<ret.article.length;i++){
                                allData.push(ret.article[i]);
                            }
                            _this.listData = allData;
                        }else{
                            Toast({
                                message: '没有更多了！',
                                position: 'middle',
                                duration: 5000
                            })
                        }
                    }else{
                        _this.listData = ret.article;
                    }
                    _this.curPages = pages;
                    
                    _this.closeLoading(600);
                } else {
                    // api.alert({ msg: '失败' });
                    Toast({
                        message: '加载失败！',
                        position: 'middle',
                        duration: 5000
                    })
                }
            })
        },
        loadings:function(){
            Indicator.open({
                text: '加载中...',
                spinnerType: 'fading-circle'
            });
        },
        closeLoading:function(times){
            if(times){
                setTimeout(function(){
                    Indicator.close();
                },times)
            }else{
                Indicator.close();
            }
            
        }
    }
}
</script>
<style>
    .banner{
        height: 200px;
    }
    .banbox img{
        width: 100%;
        height: auto;
    }
    .hlist{
        margin-top: 30px;
    }
    .hlist_box{
        padding: 20px;
        border-bottom: 1px solid rgb(240, 240, 240);
    }
    .hlist_box a{
        /* display: block; */
        position: relative;
        display: -webkit-box;
        display: -webkit-flex;
        display: flex;
        flex-wrap: wrap;
        color: rgb(69, 69, 69);
    }
    .hlist_box a img{
        display: block;
        width: 100px;
        height: 100px;
        object-fit:cover;
        margin-right: 20px;
    }
    .hlist_box a div{
        -webkit-box-flex: 1;
        -webkit-flex: 1;
        flex: 1;
    }
    .hlist_box a h4{
        font-size: 16px;
        font-weight: normal;
    }
    .hlist_box a div p{
        margin-top: 10px;
        font-size: 14px;
    }
    .hlist_box a p span:first-of-type{
        background-color: #6cf;
        color: white;
        font-size: 12px;
        margin-right: 30px;
        padding: 2px 8px;
        border-radius: 4px;
    }
</style>


