            </router-link></li>
            <li class="flex1" :class="curIndex==2?'active':''" @click="curtips('此功能暂未开发！')"><router-link :to="{path:'/apiclouds'}">
                <div class="fimgs">
                    <img src="@/assets/image/main2.png" alt=""><img src="@/assets/image/main2_1.png" alt="">
                </div>
                <p>APICloud</p>
            </router-link></li>
            <li class="flex1" :class="curIndex==3?'active':''" @click="curtips('此功能暂未开发！')"><router-link :to="{path:'/others'}">
